.Contact {
    color: white !important;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 auto;
    flex-direction: column;
    padding: 5rem;
    flex-wrap: wrap;
    box-sizing: border-box;
}
.Contact-buttons {
    display: flex;
    justify-content: center;
}
.Contact h2 {
    width: 60%;
    font-weight: 300;
    margin-bottom: 50px;
    line-height: 40px;
}
#first-contact-link {
    margin-right: 20px;
}
.Contact-link {
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    border: 1px solid white;
    font-size: 1.5rem;
    background: linear-gradient(45deg, #111f96, #0033f1);
    width: 180px;
    padding: 10px 20px;
    border-radius: 4px;
    transition: all .5s ease;
    box-sizing: content-box;
}
.Contact-link:hover {
    color: #0033f1;
    background: white;
}
.Contact-link div h3, .Contact-link div h4 {
    margin: 0;
}
.Contact-link div h4 {
    font-size: .7rem;
    font-weight: 300;
}
.Contact h1 {
    font-size: 2.5rem;
    text-transform: uppercase;
    font-weight: bold;  
    letter-spacing: 4px;
    font-size: 3rem;
}
.Contact-sub {
    display: flex;
    justify-content: space-between;
    height: 100%;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
}
.Contact-email {
    color: white;
    text-decoration: none;
}
.Contact-linkedin {
    text-decoration: none;
    color: white;
}
a.contactA svg {
    color: white !important;
}
.MuiIcon-root {
    color: white !important;
}
.MuiPaper-root {
    color: white !important;
}
.Contact-email, .Contact-linkedin, .Contact-phone {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 30px 0;
}
