@media all and (max-width: 906px) {
    .Contact {
        padding: 5px;
    }
}
@media all and (max-width: 762px) {
    .Contact h1 {
        font-size: 2.5rem;
        margin-bottom: 10px;
    }
    .Contact-buttons {
        display: block;
    }
    #first-contact-link {
        margin-bottom: 20px;
        margin-right: 0;
    }
    .Contact h2 {
        margin-bottom: 25px;
        width: 90%;
    }
}